  //open POI Modal
  openPoiModal() {
    let pointInterest = new PointInterest();
    pointInterest.address = this.addresspoi;
    pointInterest.coordinate.lat = this.latpoi;
    pointInterest.coordinate.lng = this.lngpoi;
    let PoiModal = this.modalCtrl.create(Poi, {
      pointInterest
    });
    PoiModal.present();
    PoiModal.onDidDismiss(form => {
      if (form.pointinterest.name != null) {
        this.dataManagementService.addPointInterest(form.pointinterest).subscribe(pointInterest => {
          if (pointInterest.type == "MARKER") {
            let circle = L.circle(pointInterest.coordinate, {
              color: 'red',
              fillColor: '#f03',
              fillOpacity: 0.1,
              radius: pointInterest.ray
            });
            this.mapService.addCircle(circle);
            setTimeout(() => {
              this.mapService.removeCirclesFromMap();
            }, 3000);
          }
          this.dataManagementService.pointInterests.push(pointInterest);
        }, (err) => {});
      }
    });
  }

