import { imagesDir } from './../../../providers/global.config';
import { NavController, NavParams,AlertController, ModalController, LoadingController, ViewController, MenuController, ToastController } from 'ionic-angular';
import { Component , OnInit,Host,AfterViewInit} from '@angular/core';
import { DataManagementService } from './../../../providers/data-management.service';
import { GroupService } from '../../../providers/group.service';
import { Groupe,Driver } from '../../../objects/index';
import { DriverService } from '../../../providers/driver.service';
import { marginTop } from '../../../providers/global.config';


@Component({
    selector: 'page-chauffeurs',
    templateUrl: 'chauffeurs.html',
  })


export class DriverComponent {

      marginTop=marginTop;
    // pag vars
      public maxSize:number = 5;
      public bigTotalItems:number = 175;
      public bigCurrentPage:number = 1;
      public numPages:number = 0;
      itemsPerPage = 25;
      drivers:Driver[]=[];
      constructor(public menu:MenuController,
      private driverService:DriverService) { 
            this.bigCurrentPage = this.driverService.bigCurrentPage;
      }



      ngOnInit() {
        this.getListDrivers(this.bigCurrentPage-1,this.itemsPerPage);   
      }
      getListDrivers(page:number,size:number){
        this.driverService.bigCurrentPage = this.bigCurrentPage;
        this.driverService.getDriversByPageAndSize(this.bigCurrentPage-1,this.itemsPerPage).subscribe(drivers => {
        this.drivers=drivers.content;
        this.driverService.drivers=this.drivers;
        this.bigTotalItems=drivers.totalElements;
       
      });
      
      }
      openmenu(){
        this.menu.open();
      
      }
}


